<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Update GeneralSetting
      </div>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-text-field v-model="form.GSName" :loading="loading" label="Product Name" :error-messages="errors.ProductName"
        outlined></v-text-field>
      <v-text-field v-model="form.GSValue" :loading="loading" label="FinancePurpose" :error-messages="errors.FinancePurpose"
        outlined></v-text-field>
      <v-btn color="primary" class="mr-4" :loading="data.loading" @click="update(form)">
        SIMPAN
      </v-btn>

      <v-btn color="error" class="mr-4">
        BATAL
      </v-btn>
    </v-card>
    <v-dialog
      v-model="loading"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          Mohon menunggu...
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import AuthenticationService from '@/services/AuthenticationService'

export default {
  name: 'ProductEdit',
  data: () => ({
    loading: false,
    errors: {},
    form: {
      id: '',
      GSName: '',
      GSValue: ''
    }
  }),
  mounted () {
    this.edits()
  },
  computed: {
    ...mapState('generalsetting', {
      data: (state) => state
    })
  },
  methods: {
    ...mapActions('generalsetting', [
      'update'
    ]),
    async edits () {
      this.loading = true
      const resp = await AuthenticationService.get('cms/generalsetting/show/' + this.$route.params.id)
      this.form.id = resp.data.data.id
      this.form.GSName = resp.data.data.GSName
      this.form.GSValue = resp.data.data.GSValue
      this.loading = false
    }
  }
}
</script>
